/* nav bar */
.navbar_top_menu_container{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    width: 100%;
    justify-content: center;
}

.navbar_top_menuitem{
    color: white;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.15em;
}

.navbar_top_menuitem:hover{
    color: #B69530;
}

.navbar_top_menuitem.active{
    color: #B69530;
}

/* sidebar menu */
.navbar_left_sticky_box{
    align-items: flex-end;
    border-right: 5px solid #B69530;
}

.navbar_left_sticky_menuitem{
    color: white;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.15em;
}

.navbar_left_sticky_menuitem:hover{
    color: #B69530;
}

.navbar_left_sticky_menuitem.active{
    color: #B69530;
}

/* volume page */
.volume_page{
    padding: 5rem;
    /* height: 100vh; */
}

.section_title_text_onblack{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 60px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
}

.tile_withimage_horizontal{
    height: 30vh;
}

.volume_horizonal_image{
    object-fit: cover;
    height: 30vh;
}

.tile_withimage_horizontal_subtitle{
    font-family: Roboto;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.75);
}

.masonry{
    margin: 1.5em 0;
    padding: 0;
    -moz-column-gap: 1.5em;
    -webkit-column-gap: 1.5em;
    column-gap: 1.5em;
    font-size: .85em;
    column-count: 2;
}

.masonry-item{
    display: inline-block;
    margin: 0 0 1.5em;
    width: 100%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.18);
    border-radius: 0px;
    -moz-border-radius: 3px;
}

/* media queries */

@media (min-width: 200px) {
    /* banner */
    .homepage_banner_div{
        padding: 2rem;
    }
    .banner_title_big{
        font-family: Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        /* line-height: 90px; */
        display: flex;
        align-items: center;
        color: #FFFFFF;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    .banner_subtitle{
        font-family: Roboto;
        font-style: normal;
        font-weight: 300;
        font-size: 10px;
        line-height: 15px;
        display: flex;
        align-items: center;
        letter-spacing: 0.05em;
        color: #FFFFFF;
    }

    .arrowbutton_down_withtext_animated{
        display: flex;
        justify-content: center;
        font-size: 11px;
    }
}

